<template>
  <div class="note">
    <div class="drag">
      <select class="add_category" v-model="category">
        <option value="false" >Не обрано</option>
        <option v-for="(category, index) in categories" :key="index" :value="category.id"
        >
          {{ category.name }}
        </option>
      </select>
      <div class="delete_button" @click="deleteNote(board.notes[noteIndex].id)"></div>
    </div>
    <div class="text_field">
      <textarea
        class="text_area"
        placeholder="Напишіть вашу нотатку"
        v-model="text"
      >
      </textarea>
    </div>
  </div>
</template>
<script>
export default {
  props: ["noteIndex", "filtered"],
  data() {
    return {
      board: "",
      text: "",
      category: "",
    };
  },
  methods: {
    updateLocalBoard() {
      this.board = this.$store.getters["getBoard"];
      this.text = this.board.notes[this.noteIndex].text;
      this.category = this.board.notes[this.noteIndex].category;
    },
    deleteNote(id) {
      this.$store.dispatch("deleteNote", id);
      this.updateLocalBoard();
    },
  },
  computed: {
    categories() {
      return this.$store.getters["getCategories"];
    },
  },
  watch: {
    filtered() {
      this.updateLocalBoard();
    },
    text() {
      this.board.notes[this.noteIndex].text = this.text;
      this.$store.dispatch("updateBoard", this.board);
    },
    category() {
      this.board.notes[this.noteIndex].category = this.category;
      this.$store.dispatch("updateBoard", this.board);
    },
  },
  created() {
    this.updateLocalBoard();
  },
};
</script>