import Vue from 'vue'
import Vuex from 'vuex'

import { initializeApp } from 'firebase/app'
import {
  getDoc,
  getDocs,
  collection,
  doc,
  getFirestore,
  setDoc,
  // deleteDoc
} from 'firebase/firestore'
import {
  getAuth,
  signOut,
  signInWithEmailAndPassword,
  createUserWithEmailAndPassword,
} from 'firebase/auth'


const APP = initializeApp({
  apiKey: "AIzaSyCIKNeoq69O5PHKWYiRvdofaz5ZuZwC-Yk",
  authDomain: "notes-758d0.firebaseapp.com",
  projectId: "notes-758d0",
  storageBucket: "notes-758d0.appspot.com",
  messagingSenderId: "304411680834",
  appId: "1:304411680834:web:fae6c0eb41311db55a4ddb"
});

const DB = getFirestore(APP);
const AUTH = getAuth(APP);

function getDocFromDB(c, d) {
  return getDoc(doc(DB, c, d))
}
function getCollectionFromDB(c) {
  return getDocs(collection(DB, c))
}

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    uid: null,
    board: null,
    categories: [],
    isLoggedIn: false,
    isReg: false,

  },
  getters: {
    getBoard(state) {
      return state.board;
    },
    getCategories(state) {
      return state.categories;
    },
    isLoggedIn(state) {
      return state.isLoggedIn;
    },
    isReg(state) {
      return state.isReg;
    }
  },
  actions: {
    //Робота з нотатками
    fetchBoard(context) {
      getDocFromDB('Boards', context.state.uid)
        .then(responce => {
          context.state.board = null;
          context.state.board = responce.data();
        })
    },
    updateBoard(context, board) {
      return setDoc(doc(DB, 'Boards', context.state.uid), board)
    },
    addNote(context, note) {
      context.state.board.notes.push(note);
      return setDoc(doc(DB, 'Boards', context.state.uid), context.state.board)
    },

    deleteNote(context, id) {
      context.state.board.notes = context.state.board.notes.filter((note) => note.id !== id);
      setDoc(doc(DB, 'Boards', context.state.uid), context.state.board)
    },
    fetchCategories(context) {
      getCollectionFromDB('Categories')
        .then(responce => {
          context.state.categories = [];
          responce.forEach(document => {
            context.state.categories.push(document.data());
          })
        })
    },

    //логін
    login(context, userCred) {
      if (userCred.email.length && userCred.password.length) {
        signInWithEmailAndPassword(AUTH, userCred.email, userCred.password)
          .then((cred) => {
            context.state.isLoggedIn = true;
            context.state.uid = cred.user.uid;
            context.dispatch('fetchBoard');
          })
          .catch((error) => {
            console.error(error)
            document.querySelector('.incorect_data').style.display = "inline";
          })
      }
    },
    signout(context) {
      signOut(AUTH)
        .then(() => {
          context.state.isLoggedIn = false;
          
        })
        .catch((error) => {
          console.error(error)
        });
    },

    updateIsReg(context) {
      if (context.state.isReg) {
        context.state.isReg = false;
      } else {
        context.state.isReg = true
      }
    },
    signup(context, userCred) {
      let userData = {
        name: userCred.name,
        phone: userCred.phone,
        email: userCred.email,
        uid: '',
        notes: []
      }
      createUserWithEmailAndPassword(AUTH, userCred.email, userCred.password)
        .then((cred) => {
          userData.uid = cred.user.uid;
          setDoc(doc(DB, 'Boards', cred.user.uid), userData)
        })
        .catch((error) => {
          console.error(error.code)
          console.error(error.message);
        });
    }

  },
})












