<template>
  <div id="app">
    <div class="reg-window" v-if="!isLoggedIn && isReg">
      <div class="reg_box">
        <h3 class="title">Sign up</h3>
        <div class="form">
          <p class="incorect_data">*incorrect data</p>
          <input type="text" placeholder="Name" v-model="registerUserCred.name" required>
          <input type="email" placeholder="Email" v-model="registerUserCred.email" required>
          <input type="text" placeholder="Phone number" v-model="registerUserCred.phone" required>
          <input type="password" placeholder="Password" v-model="registerUserCred.password" required>
          <input type="password" placeholder="Repeat password" v-model="registerUserCred.rep_password" required>
        </div>
        <div class="actions">
          <button class="btn_signup" @click="signup">Sign up</button>
        </div>
        <hr>
        <p class="p_login"><span>Already a user?</span><a @click="toogleRegLog">Sign in</a></p>
      </div>
    </div>
    <div class="login-window" v-if="!isLoggedIn && !isReg">
      <div class="login_box">
        <h3 class="title">Sign in</h3>
        <div class="form">
          <p class="incorect_data">*incorrect email or password</p>
          <input type="email" placeholder="Email" v-model="userCred.email" required>
          <input type="password" placeholder="Password" v-model="userCred.password" required>
        </div>
        <div class="actions">
          <button class="btn_i" @click="login">Login</button>
          <button class="btn_u" @click="toogleRegLog">Sign Up</button>
        </div>
      </div>
    </div>
    <template v-if="isLoggedIn">
      <div class="menu">
        <img src="./assets/img/logo.png" />
        <div class="add_note" @click="addNote"></div>
        <div class="filter" @click="toogleFilter"></div>
        <div class="exit" @click="signout"></div>
        <div class="drop_category">
          <h3>Обери категорії:</h3>
          <span v-for="(category, index) in categories" :key="index">
            <input type="checkbox" :value="category.id" v-model="checkedCategories"/>{{ category.name }}
          </span>
        </div>
      </div>
      <div class="note_field" @mousedown="mousedown" @mouseup="mouseup" @mousemove="mousemove">
        <template v-if="board">
          <NoteBlock
            v-for="(note, index) in filteredNotes"
            :key="index"
            :note-Index="board.notes.indexOf(note)"
            :noteIndex="board.notes.indexOf(note)"
            :style="'left: ' + note.coords.x + 'px; top: ' + note.coords.y + 'px;'"
            :filtered="filteredNotes"
          />
        </template>
      </div>  
    </template> 
  </div>
</template>

<script>
import NoteBlock from "./components/NoteBlock.vue";

export default {
  name: "App",
  components: {
    NoteBlock,
  },
  data() {
    return {
      registerUserCred: {
        name: "",
        email: "",
        phone: '',
        password: "",
        rep_password: ""
      },

      userCred: {
        email: "",
        password: "",
      },
      startCoords: {
        x: 0,
        y: 0,
      },
      currentCoords: {
        x: 0,
        y: 0,
      },
      selectedNoteNumber: null,
      action: false,
      checkedCategories: [],
    };
  },
  methods: {
    login() {
      if (
        this.userCred.email !== "" &&
        this.userCred.password !== "" &&
        /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.userCred.email)
      ) {
        this.$store.dispatch("login", this.userCred);
        this.userCred.email = "";
        this.userCred.password = "";
        document.querySelector(".incorect_data").style.display = "none";
      } else {
        document.querySelector(".incorect_data").style.display = "inline";
      }
    },
    signout() {
      this.$store.dispatch("signout");
      setTimeout(function(){
          location.reload()
      }, 1)
    },
    signup() {
      if (
        this.registerUserCred.name !== "" &&
        this.registerUserCred.email !== "" &&
        this.registerUserCred.phone !== "" &&
        /^\+\d{12}$/.test(this.registerUserCred.phone) &&
        this.registerUserCred.password !== "" &&
        this.registerUserCred.password.length >= 6 &&
        this.registerUserCred.rep_password !== "" &&
        this.registerUserCred.password == this.registerUserCred.rep_password &&
        /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.registerUserCred.email)
      ) {
        this.$store.dispatch('signup', this.registerUserCred);
        this.registerUserCred.name = '';
        this.registerUserCred.email = '';
        this.registerUserCred.phone = '';
        this.registerUserCred.password = '';
        this.registerUserCred.rep_password = '';
        document.querySelector(".incorect_data").style.display = "none";
        this.toogleRegLog();
      } else {
        document.querySelector(".incorect_data").style.display = "inline";
      }
    },

    //Відкриття та закриття вікна фільтрації
    toogleFilter() {
      if (
        window
          .getComputedStyle(document.querySelector(".drop_category"))
          .getPropertyValue("visibility") == "hidden"
      ) {
        document.querySelector(".drop_category").style.visibility = "visible";
        document.querySelector(".drop_category").style.opacity = "1";
        document
          .querySelector(".drop_category")
          .style.setProperty("z-index", "10");
      } else {
        document.querySelector(".drop_category").style.visibility = "hidden";
        document.querySelector(".drop_category").style.opacity = "0";
        document
          .querySelector(".drop_category")
          .style.setProperty("z-index", "-1");
      }
    },

    toogleRegLog() {
      this.$store.dispatch("updateIsReg");    
      document.querySelector(".incorect_data").style.display = "none";
    },
    //Створення нотатки
    addNote() {
      this.$store.dispatch("addNote", {
        coords: {
          x: 0,
          y: 0,
        },
        text: "",
        id: "n" + Date.now(),
        category: false,
      });
    },

    // 3 методи для переміщення нотаток
    mousedown(event) {
      if (event.target.classList.contains("drag")) {
        this.action = true;
        this.selectedNoteNumber =
          event.target.parentNode.getAttribute("note-Index");
        this.startCoords.x = event.pageX;
        this.startCoords.y = event.pageY;
        this.currentCoords.x =
          this.board.notes[this.selectedNoteNumber].coords.x +
          (event.pageX - this.startCoords.x);
        this.currentCoords.y =
          this.board.notes[this.selectedNoteNumber].coords.y +
          (event.pageY - this.startCoords.y);
      }
    },
    mouseup() {
      if (this.action) {
        this.action = false;
        this.board.notes[this.selectedNoteNumber].coords.x =
          this.currentCoords.x;
        this.board.notes[this.selectedNoteNumber].coords.y =
          this.currentCoords.y;
        this.$store.dispatch("updateBoard", this.board);
      }
    },
    mousemove(event) {
      if (this.action) {
        const notes_field_width =
          document.querySelector(".note_field").offsetWidth;
        const notes_field_height =
          document.querySelector(".note_field").offsetHeight;
        const note_width = document.querySelector(".note").offsetWidth;
        const note_height = document.querySelector(".note").offsetHeight;
        this.currentCoords.x =
          this.board.notes[this.selectedNoteNumber].coords.x +
          (event.pageX - this.startCoords.x);
        this.currentCoords.y =
          this.board.notes[this.selectedNoteNumber].coords.y +
          (event.pageY - this.startCoords.y);
        if (this.currentCoords.x <= 0) {
          this.currentCoords.x = 0;
        }
        if (this.currentCoords.x >= notes_field_width - note_width) {
          this.currentCoords.x = notes_field_width - note_width;
        }
        if (this.currentCoords.y <= 0) {
          this.currentCoords.y = 0;
        }
        if (this.currentCoords.y >= notes_field_height - note_height) {
          this.currentCoords.y = notes_field_height - note_height;
        }
        document.querySelector(
          '.note[note-Index="' + this.selectedNoteNumber + '"]'
        ).style.left = this.currentCoords.x + "px";
        document.querySelector(
          '.note[note-Index="' + this.selectedNoteNumber + '"]'
        ).style.top = this.currentCoords.y + "px";
      }
    },
  },
  computed: {
    board() {
      return this.$store.getters["getBoard"];
    },
    categories() {
      return this.$store.getters["getCategories"];
    },
    filteredNotes() {
      return this.checkedCategories.length
        ? this.board.notes.filter((note) => {
            return ~this.checkedCategories.indexOf(note.category);
          })
        : this.board.notes;
    },
    isLoggedIn() {
      return this.$store.getters["isLoggedIn"];
    },
    isReg() {
      return this.$store.getters["isReg"];
    },
  },
  created() {
    this.$store.dispatch("fetchCategories");
  },
};
</script>

<style lang="less">
@import "./assets/less/index.less";
</style>
